.collapse {
  overflow: hidden;
  transition: height 500ms cubic-bezier(0.76, 0, 0.24, 1);
}
.collapse:not(.active) {
  height: 0;
}

[data-collapse] .collapse-arrow,
[data-toggler] .collapse-arrow {
  transition: 500ms all cubic-bezier(0.76, 0, 0.24, 1);
}
[data-collapse].active .collapse-arrow,
[data-toggler].active .collapse-arrow {
  transform: rotate(-180deg);
}

.link-effect {
  position: relative;
  white-space: nowrap;
}
.link-effect::before, .link-effect::after {
  bottom: 2px;
  width: 100%;
  transform: scaleX(0);
  height: 1px;
  will-change: transform;
  left: 0;
  background-color: currentColor;
  transition: transform 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.link-effect::before {
  opacity: 0;
  transform-origin: left;
}
.link-effect::after {
  opacity: 1;
  transform-origin: right;
}
.link-effect:hover::before, .link-effect:hover::after {
  transform: scaleX(1);
}
.link-effect:hover::before {
  opacity: 1;
}
.link-effect:hover::after {
  opacity: 0;
}

.animate-up {
  transform: translateY(15px);
  opacity: 0;
  transition: opacity 0.5s cubic-bezier(0.42, 0, 0.002, 1), transform 0.65s cubic-bezier(0.42, 0, 0.002, 1);
}

.animate-down {
  transform: translateY(-100%);
  opacity: 0;
  transition: opacity 0.5s cubic-bezier(0.42, 0, 0.002, 1), transform 0.65s cubic-bezier(0.42, 0, 0.002, 1);
}

.btn {
  cursor: pointer;
  font-family: "Poppins", sans-serif;
  border: none;
  transition: 500ms all cubic-bezier(0.76, 0, 0.24, 1);
  display: flex;
  align-items: center;
  white-space: nowrap;
  justify-content: center;
  flex-shrink: 0;
}
.btn svg {
  flex-shrink: 0;
}
.btn svg * {
  transition: 500ms background-color cubic-bezier(0.76, 0, 0.24, 1), 300ms color cubic-bezier(0.76, 0, 0.24, 1), 300ms border-color cubic-bezier(0.76, 0, 0.24, 1), 300ms opacity cubic-bezier(0.76, 0, 0.24, 1), 300ms box-shadow cubic-bezier(0.76, 0, 0.24, 1), 300ms stroke cubic-bezier(0.76, 0, 0.24, 1), 300ms fill cubic-bezier(0.76, 0, 0.24, 1);
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.btn:active {
  transform: scale(0.95);
}

.btn-primary {
  font-size: 20px;
  text-decoration: none;
  position: relative;
  padding: 14px 29px;
  border-radius: 0;
  color: #FFFFFF;
  border: 1px solid #f9a280;
  background-color: #f9a280;
  overflow: hidden;
  gap: 10px;
  border-radius: 5px;
  font-weight: 500;
  text-transform: uppercase;
  cursor: alias;
}
.btn-primary::after {
  content: "";
  position: absolute;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -150%;
  background: linear-gradient(to bottom, rgba(229, 172, 142, 0), rgba(255, 255, 255, 0.5) 50%, rgba(229, 172, 142, 0));
  transform: rotateZ(60deg) translate(-5em, 7.5em);
